import React , {useState} from 'react';
import { db } from '../firebase'
import { id32 } from '@jsweb/randkey'
import firebase from 'firebase'

import '../CSS/AddButton.css'

const AddButton = ({uid}) => {
	const [title , setTitle] = useState('')
	const [desc , setDesc] = useState('')

	const onClick = () => {
		if (title !== '' && title !== undefined) {
			const key = id32();
			const myTimestamp = firebase.firestore.Timestamp.fromDate(new Date());
			db.collection("userTodos").doc(uid).collection("todos").doc(key).set({
				date: myTimestamp,
				title: title,
				desc: desc,
				done: false,
				id: key
			})
			.then( docRef => {
				setTitle("")
				setDesc("")
			})
		}
	}

  return (
  	<div className="addBtn-main">
  		<div className="add-btn-inp-main">
  			<input onChange={(e) => setTitle(e.target.value)} value={title} placeholder="Title" className="add-btn-inp"/>
  			<input onChange={(e) => setDesc(e.target.value)} value={desc} placeholder="Description" className="add-btn-inp addBtn-desc"/>
  		</div>
	    <div onClick={onClick} className="addBtn-main-btn">
	    	<h1 className="addBtn-m-text">+</h1>
	    </div>
	  </div>
  )
}

export default AddButton;