import React from 'react';
import {dayBuilder , monthBuilder , yearBuilder} from '../Logic/datebuilder.js'

import '../CSS/Header.css'

const Header = () => {
	var utc = new Date().toJSON().slice(0,10).replace(/-/g,'/')
	const day = dayBuilder( new Date() )
	const date = utc[ utc.length - 2 ] + utc[ utc.length - 1 ];
	const month = monthBuilder( new Date() );
	const year = yearBuilder( new Date() );

  return (
    <div className="header-main">
    	<div className="header-left">
    		<div className="header-bigNum">
    		{date}
    		</div>

    		<div className="header-left-small">
    			<h3 className="h-l-s-top">{month}</h3>
    			<h3 className="h-l-s-bottom">{year}</h3>
    		</div>
    	</div>

    	<div className="header-right">
    		{day}
    	</div>
    </div>
  )
}

export default Header;