import React , {useState} from 'react';
import Tick from '../images/tick.png'
import X from '../images/x.png'
import {db} from '../firebase'

import '../CSS/ToDo.css'

const ToDo = ( {done , uid , title , desc , id} ) => {

  const onDelete = () => db.collection("userTodos").doc(uid).collection("todos").doc(id).delete()
  
  const onMarkDone = () => {
    db.collection("userTodos").doc(uid).collection("todos").doc(id).set({
      done: !done
    }, {merge: true})
  }

  const [open , setOpen] = useState(false);
  const onClick = () => setOpen(!open)

  return (
    <div className={`toDo-main ${ open === true ? 'open' : 'closed' } ${ done === true ? 'done' : '' }`}>
      <h3 onClick={onClick} className={`toDo-text ${ open === true ? 'toDo-t-o' : 'toDo-t-c' } ${ done === true ? 'toDo-t-d' : '' }`}>
      {title}
      {open === true ? (
        <div className={`toDo-popUp ${ done === true ? 'toDo-d-d' : '' }`}>
          {desc}
        </div>
      ) : ''}
      </h3>

      <div className="toDo-images">
        <img onClick={onMarkDone} alt="done" className="toDo-image" src={Tick}/>
        <img onClick={onDelete} alt="not-done" className="toDo-image" src={X}/>
      </div>
    </div>
  )
}

export default ToDo;