import React , {} from 'react';
import GoogleBtn from '../Components/GoogleBtn'
import { auth , provider , db} from '../firebase'
import firebase from 'firebase'
import '../CSS/Login.css'

const Login = ( { logged , setLogged , setUser} ) => {
  const addUser = ( user ) => {
    db.collection("users").doc(user.uid).set({
      name: user.displayName,
      email: user.email,
      uid: user.uid
    })
  }

  const addUserToTodos = (uid) => {
    db.collection("userTodos").doc(uid).set({
      initialField: 'initialField'
    })
    db.collection("userTodos").doc(uid).collection("todos").doc("initialDoc").set({
      initialField: 'initialField'
    })
  }

	const onClick = () => {
    firebase.auth().setPersistence(firebase.auth.Auth.Persistence.LOCAL)
    .then(function() {
      return auth.signInWithPopup(provider)
      .then( res => {
        setLogged(true)
        setUser(res.user)
        addUser(res.user)
        addUserToTodos(res.user.uid)
        console.log(res.user.uid)
      })
      .catch( err => console.log(err.message) )
    })
    .catch(function(error) {
      var errorCode = error.code;
      var errorMessage = error.message;
      console.log( errorCode + " " + errorMessage )
    });
	}

  return (
    <div className="log-main">
      <div className="log-whiteThingy">
      	<h1 className="log-title">Log In</h1>
        <h2 className="log-desc">To use the app</h2>
      </div>
      <GoogleBtn onClick={onClick}/>
    </div>
  )
}

export default Login;