import React , { useState , useEffect } from 'react';
import ToDo from '../Components/ToDo'
import { db } from '../firebase'

const Todos = ( { uid } ) => {
	const [todos , setTodos] = useState([])

	const getTodos = () => {
		db.collection("userTodos").doc(uid).collection("todos").orderBy("date").onSnapshot( querySnapshot => {
      let todos = [];
      querySnapshot.forEach( doc => {
        todos.push(doc.data());
      });
      setTodos(todos)
    });
	}

	useEffect(  () => {
		getTodos();
  // eslint-disable-next-line
	}, [] )

  return (
    <div className="scrollable">
    	{ 
        // eslint-disable-next-line
        todos.map( (todo , ind) => {
        if(todo.initialField !== 'initialField') return <ToDo done={todo.done} uid={uid} key={ind} title={todo.title} desc={todo.desc} id={todo.id}/>
      } ) }
    </div>
  )
}

export default Todos;